@import 'fonts';
@import 'mixins';
@import 'vars';
@import 'generated/sprite-svg';
@import 'libs/slick';
@import 'libs/slick-theme';
html,
body {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  @include rfs(20);
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

.wrapper {
  width: 100%;
  font-family: $font-primary;
  color: #4c4d4f;
  @media (max-width: 992px) {
    .container {
      max-width: 98%;
    }
  }
}
.contr {
  max-width: 1200px;
  margin: 0 auto;
}
// HEADER
.header {
  padding: 1.5rem 0;
  color: #010101;
  @media only screen and (max-width: 992px) {
    padding: 1em 0;
  }
  @media only screen and (max-width: 480px) {
    padding: 0.5em 0;
  }
  @include rfs(24);
  .navbar {
    max-width: 1200px;
    margin: 0 auto;
  }
  &__logo {
    .icon-main-logo {
      width: 5.75rem;
      @media only screen and (max-width: 768px) {
        width: 4rem;
      }
    }
  }
  .main-menu {
    @media only screen and (max-width: 992px) {
      border-top: 1px solid #2287b9;
      border-bottom: 1px solid #2287b9;
      .nav-item {
        margin: 0.2rem 0;
        text-align: center;
      }
    }
  }
  .nav-item {
    &:not(:last-child) {
      @media only screen and (min-width: 992px) {
        margin-right: 1em;
      }
    }
    &:hover {
      .nav-link {
        background-color: #fff;
        color: #2287b9;
      }
    }
    .dropdown-toggle::after {
      display: none;
    }
    &.nav-item--current a {
      color: #000;
    }
  }
  .nav-link {
    padding: 0.5em 0.6em;
    font-family: $font-primary;
    font-size: 0.9rem;
    font-weight: 500;
    line-height: 0.48;
    letter-spacing: 0.9px;
    text-transform: uppercase;
    color: #777;
    transition: all 0.3s ease;
  }
  .dropdown-menu {
    min-width: 9rem;
    padding: 1em 0 0 0;
    margin: 0;
    border: none;
    border-radius: 0;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1), 0 8px 15px 0 rgba(0, 0, 0, 0.1);
  }
  .dropdown-item {
    color: #2287b9;
    padding: 0.25rem 0.6em;
    font-size: 1em;
    &:hover {
      color: #2287b9;
      background: transparent;
    }
    &:active {
      color: #ffffff;
      background-color: lighten(#2287b9, 10%);
    }
  }
}
.navbar-toggler {
  border: none;
  color: #fff;
  &:focus {
    outline: none !important;
  }
}
.navbar-toggler-icon {
  width: 2rem;
  height: 2rem;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#4c4d4f' stroke-width='2' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  @media only screen and (min-width: 480px) {
    margin-bottom: 0.7rem;
  }
}
// Simple section
.content-section {
  padding: 4rem 0;
  background-color: #fff;
  .content__title {
    font-family: $font-primary;
    font-size: 1.5rem;
    font-weight: bold;
    line-height: 1.6;
    color: #2287b9;
  }
  .carousel {
    margin-top: 2rem;
  }
}
.carousel-section {
  background-color: #f8f8f8;
  .content__title {
    color: #305161;
    font-size: 1.5rem;
  }
}

// Hero
.hero {
  position: relative;
  display: flex;
  min-height: 42vw;
  &-inner {
    z-index: 2;
    width: 100%;
  }
  .contr {
    max-width: 1100px;
    height: 100%;
    margin: 0 auto;
  }
  &__row {
    height: 100%;
    padding: 5.35rem 0 4rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__title {
    font-size: 3.625rem;
    font-weight: bold;
    line-height: 1.14;
    letter-spacing: normal;
    color: #ffffff;
    text-shadow: 5.3px 4.6px 9.6px rgba(0, 0, 0, 0.22);
    @media only screen and (max-width: 480px) {
      font-size: 2rem;
    }
  }
  &__box {
    max-width: 450px;
    padding: 3rem 1.2rem 2rem;
    font-size: 1.2rem;
    color: #777777;
    text-align: center;
    font-weight: 700;
    background-color: rgba(255, 255, 255, 0.8);
  }
  @media only screen and (max-width: 768px) {
    &__title,
    &__box {
      margin: 0 auto;
      text-align: center;
    }
    &__title {
      margin-bottom: 2rem;
      font-size: 2.5rem;
    }
  }
}
// end Hero
// Products
.products {
  &__title {
    margin: 1.4rem 0 1rem;
    font-size: 1.22rem;
    font-weight: bold;
    line-height: 1.18;
    letter-spacing: 1.8px;
    text-align: center;
    color: #808285;
  }
  &__inner {
    background-color: #f8f8f8;
    padding-bottom: 1.3rem;
  }
  &__box {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 1px solid #e6e6e6;
    &:after {
      content: '';
      display: inline-block;
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 42%;
      height: 1px;
      background-color: #6d6d6d;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin: 1rem 0 0;
    @media only screen and (max-width: 992px) {
      width: 33.333%;
    }
    @media only screen and (max-width: 580px) {
      width: 50%;
    }
    @media only screen and (max-width: 480px) {
      width: 90%;
    }
    img {
      max-width: 100%;
    }
  }
  &__name {
    margin-top: 1rem;
  }
}
// CTA
.cta {
  padding: 2.4rem 2rem 2rem;
  &__title {
    margin-bottom: 1.4rem;
    font-size: 1.8rem;
    color: #2287b9;
    font-weight: 700;
  }
  &__text {
    font-size: 1.2rem;
    color: #8e8e8e;
  }
  &--white {
    .cta__title,
    .cta__text {
      color: #fff;
    }
  }
}
// end CTA
.tiles {
  padding-bottom: 4.5rem;
  .cta {
    display: flex;
    flex-direction: column;
    margin-top: 2.5rem;
    min-height: 37.5rem;
    &__title {
      color: #8e8e8e;
    }
  }
  .cta--tall {
    min-height: 44.5rem;
  }
  .cta--white {
    color: #fff;
    .cta__title,
    .cta__text {
      color: inherit;
    }
  }
  .cta--lightgrey {
    color: #aaa9aa;
    .btn {
      color: #aaa9aa;
      border-color: #aaa9aa;
    }
  }
  .max-width-eat {
    padding-top: 1rem;
    .cta__title {
      max-width: 315px;
    }
    .cta__text {
      line-height: 1;
      max-width: 256px;
    }
  }
}
.pigmoney {
  width: 10rem;
}
// Tiles section
.tiles {
  display: flex;
  flex-wrap: wrap;
  &__col {
    width: 50%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 300px;
    padding: 3.6rem 0;
    @media only screen and (max-width: 580px) {
      width: 100%;

      padding: 0 !important;
      .cta {
        padding: 1rem;
      }
    }
  }
}
// Brands section
.brands {
  padding: 1.9rem 0 2.9rem;
  .contr {
    max-width: 1366px;
    padding: 0 3.5vw;
  }
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media only screen and (max-width: 480px) {
      justify-content: center;
    }
  }
  &__item {
    position: relative;
    width: 31.4%;
    height: 19.7rem;
    margin-top: 1.9rem;
    padding: 1.8rem 1.2rem 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    box-shadow: 5.3px 4.6px 9.6px 3.4px rgba(0, 0, 0, 0.22);
    @media only screen and (max-width: 992px) {
      width: 48%;
    }

    @media only screen and (max-width: 480px) {
      width: 90%;
    }
    img {
      max-width: 100%;
    }
  }
  &__link {
    position: absolute;
    bottom: 1.5rem;
    right: 1.2rem;
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    line-height: 1.14;
    color: rgba(0, 0, 0, 0.5);
    .icon {
      fill: currentColor;
      font-size: 0.9em;
      margin-left: 10px;
      transform: translateY(9%);
    }
    &:hover {
      color: #2287b9;
    }
  }
  &__about-us {
    font-size: 31.5px;
    font-weight: bold;
    line-height: 1.21;
    text-align: center;
    color: #777777;
  }
}
// end Brands section

picture {
  source,
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

img {
  border: 0;
}

img:not([src]):not([srcset]) {
  visibility: hidden;
}
a {
  transition: all 0.3s ease;
  &:hover {
    text-decoration: none;
  }
}
.lazyload {
  animation-name: fade;
  animation-duration: 1s;
}
.bg-absolute {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  z-index: -1;
  background-color: #ccc;
}
.obj-pos-botttom {
  object-position: bottom;
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.with-bg {
  position: relative;
}
.btn {
  padding: 0.8em 1.25em 0.6rem;
  font-family: $font-primary;
  font-size: 16px;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: normal;
  text-align: center;
  color: #777;
  background-color: transparent;
  border-radius: 0;
  border: 2px solid #777;
  &:hover {
    color: #2287b9;
    border-color: #2287b9;
  }
  &--blue {
    color: #2287b9;
    border-color: #2287b9;
    &:hover {
      color: lighten(#2287b9, 5%);
      border-color: lighten(#2287b9, 5%);
    }
  }
  &--white {
    color: #fff;
    border-color: #fff;
    &:hover {
      color: darken(#fff, 15%);
      border-color: darken(#fff, 15%);
    }
  }
}
/* Btn Ripple Out */

@-webkit-keyframes hvr-ripple-out {
  100% {
    top: -8px;
    right: -8px;
    bottom: -8px;
    left: -8px;
    opacity: 0;
  }
}

@keyframes hvr-ripple-out {
  100% {
    top: -8px;
    right: -8px;
    bottom: -8px;
    left: -8px;
    opacity: 0;
  }
}

.hvr-ripple-out {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
}

.hvr-ripple-out:before {
  content: '';
  position: absolute;
  border: #4c4d4f solid 4px;
  border-radius: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  animation-duration: 1s;
}

.hvr-ripple-out:hover:before,
.hvr-ripple-out:focus:before,
.hvr-ripple-out:active:before {
  animation-name: hvr-ripple-out;
}

/* end of Btn Ripple Out */
// slick slider
.carousel-wrapper {
  @media only screen and (min-width: 480px) {
    margin: 0 2rem;
  }
  .slide {
    padding: 0 1rem;
    display: flex !important;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .slick-arrow {
    height: 3rem;
    background-repeat: no-repeat;
    background-position: center;
    &:before {
      content: '';
    }
  }

  .slick-next {
    right: -2rem;
    background-image: url(../img/arrow-right.svg);
    @media screen and (max-width: 600px) {
      right: 0;
    }
  }
  .slick-prev {
    background-image: url(../img/arrow-left.svg);
    left: -2rem;
    z-index: 1;
    @media screen and (max-width: 600px) {
      left: 0;
    }
  }
}
ul {
  padding-left: 0;
  li {
    list-style-type: none;
  }
}
// FOOTER
.footer {
  font-family: $font-primary;
  .footer__navbar {
    padding: 2rem 0 2.5rem;
    color: #000;
    a {
      color: inherit;
    }
    .nav-link {
      padding: 0.1rem 0;
      font-weight: 400;
      font-size: 0.9rem;
      display: inline-block;
      &:hover {
        color: #4c4d4f;
      }
    }
  }
  .footer__signup {
    @media only screen and (max-width: 580px) {
      margin: 2rem 0;
    }
    .btn {
      padding: 0.2rem 1rem 0.25rem;
      font-size: 1rem;
    }
    .btn--empty {
      border-radius: 10px;
      color: #000;
      background-color: transparent;
      border: solid 2px #000;
      &:hover {
        color: #4c4d4f;
        border-color: #4c4d4f;
      }
    }
    .form-control {
      max-width: 202px;
      padding: 0.3rem 0.75rem;
      font-size: 0.88rem;
      line-height: 1;
      color: #000;
      background-color: #f0f0f0;
      border-radius: 8.9px;
      border: none;
      &::placeholder {
        color: #000;
      }
    }
  }
  .signup__title {
    font-size: 0.88rem;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.2;
    letter-spacing: 0.8px;
  }
  .footer__logoblock {
    height: 100%;
    @media only screen and (max-width: 580px) {
      height: 4rem;
    }
  }
  .footer__main-logo {
    margin-top: auto;
    width: 11rem;
    @media only screen and (min-width: 580px) {
      margin-left: auto;
    }
    @media only screen and (max-width: 1200px) {
      width: 8rem;
    }
    @media only screen and (max-width: 480px) {
      width: 6rem;
    }
  }
  &__copy {
    padding: 1.2rem 0 1rem;
    font-size: 0.7rem;
    background-color: #305161;
    color: #fff;
    p {
      margin-bottom: 0;
    }
  }
}
