/* Btn Ripple Out */
@-webkit-keyframes hvr-ripple-out { 100% { top: -8px;
    right: -8px;
    bottom: -8px;
    left: -8px;
    opacity: 0; } }

@keyframes hvr-ripple-out { 100% { top: -8px;
    right: -8px;
    bottom: -8px;
    left: -8px;
    opacity: 0; } }

.hvr-ripple-out { display: inline-block; vertical-align: middle; -webkit-transform: perspective(1px) translateZ(0); transform: perspective(1px) translateZ(0); box-shadow: 0 0 1px transparent; position: relative; }

.hvr-ripple-out:before { content: ''; position: absolute; border: #4c4d4f solid 4px; border-radius: 0; top: 0; right: 0; bottom: 0; left: 0; animation-duration: 1s; }

.hvr-ripple-out:hover:before, .hvr-ripple-out:focus:before, .hvr-ripple-out:active:before { animation-name: hvr-ripple-out; }

/* end of Btn Ripple Out */
